.mail {
    position: relative;
    font-size: 25px;
    margin: 15px 0;
}

.mail:before, .mail:after {
    position: absolute;
    width: 10%;
    height: 3px;
    content: '';
    border-top: 1px solid #000;
    margin-top: .60em;
}

.mail:after {
    margin-left: 1%;
}

.mail:before {
    margin-left: -11%;
}

.mail span {
    background: #fff;
    padding: 0 15px;
    color: #fc4d02;
}

